body {
  height: 100%;
  width: 100vw;
  overflow-x: hidden;
}
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    scroll-behavior: smooth
}
.form__inner--flex-justify-center {
    justify-content: center;
}
.form__inner--wider {
    width: 431px;
}
.form__text--bold {
  font-weight: 600;
}
.form__title--margin-bottom {
    margin-bottom: 40px;
}
.form__title--margin-top {
    margin-top: 53px;
}
.form {
  height: 100vh;
  width: 100vw;
  display: none;
  align-items: center;
  justify-content: center;
  position: fixed;
  overflow: hidden;
  z-index: 200;
  background-color: #f6f3ef;
}
.form__close-icon {
    height: 50px;
    width: 50px;
    position: absolute;
    right: 0;
    top: 0;
    margin-top: 60px;
    margin-right: 60px;
    cursor: pointer;
}
.form__inner {
    height: 600px;
    width: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.form__link {
  text-decoration: none;
  color: #111111;
}
.form__text {
  font-family: 'Barlow', sans-serif;
  font-size: 16px;
  font-weight: 200;
  line-height: 1.4em;
  letter-spacing: 0.03em;
  margin-bottom: 26px;
}
.form__title {
  font-family: 'Barlow', sans-serif;
  font-size: 48px;
  font-weight: 300;
  line-height: 1.4em;
  letter-spacing: 0.03em;
}
.input {
    height: 40px;
    width: 300px;
    font-family: 'Barlow', sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.4em;
    outline: none;
    display: block;
    padding-left: 1em;
    padding-right: 1em;
}
.input__label {
    display: block;
    margin-bottom: 8px;
}
.input__text {
    font-family: 'Barlow', sans-serif;
    font-size: 14px;
    font-weight: 200;
    line-height: 1.4em;
}
.input--animated {
    background-color: transparent;
    z-index: 10;
    visibility: hidden;
    border: none;
    transition: 1s;
    padding: 0;
}

.active {
    width: 825px;
    transition: 0.8s;
    visibility: visible;
}

.placeholder-hide {
    opacity: 0;
    transition: 0.8s;
}

input::placeholder {
    font-family: 'Barlow', sans-serif;
    font-size: 16px;
    color: #514f45;
    opacity: 0.6;
    left: 100px;
}
.input--focused:focus {
    border-bottom: 2px solid #797767;
    padding: 3px 3px 2px 12px;
}
.input--extended-margin-bottom {
    margin-bottom: 40px;
}
.input--form {
    height: 42px;
    width: 250px;
    border: none;
    border-bottom: 1px solid #797767;
    padding: 3px 3px 3px 12px;
    margin-bottom: 22px;
    background-color: #f6f3ef;
}
.input--hovered:hover {
    border-bottom: 2px solid #797767;
    padding: 3px 3px 2px 12px;
}
.input--modal {
    width: 74px;
    color: #575757;
    border: 1px solid #c0c0c0;
    margin-bottom: 50px;
}
.button {
    height: 42px;
    width: 200px;
    padding: 8px 16px;
    cursor: pointer;
    background-color: #797767;
    border: 1px #797767 solid;
}
.button__icon {
    transition: transform 0.4s ease;
    pointer-events: none;
}

.icon-rotate {
    transform: rotate(180deg);
}
.button__text {
    font-family: 'Barlow', sans-serif;
    font-size: 16px;
    line-height: 1.4em;
    font-weight: 200;
    pointer-events: none;
    color: #FFFFFF;
}
.button--dropdown {
    min-width: 200px;
    width: fit-content;
    display: flex;
    justify-content: space-between;
    background: transparent;
    text-overflow: ellipsis;
    white-space: nowrap;
    border: 1px solid rgba(110, 119, 93, 0.4);
    margin: 25px 0 20px 0;
    padding: 8px 0;
}
.button--hovered:hover {
    opacity: 0.6;
    transition: 0.5s ease;
}
.button--form {
    width: 250px;
